import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    deviceId: "empty"
  },
  mutations: {
	  setDeviceId(state, val) {
	    state.deviceId = val
	  },
  },
  actions: {
	loadDeviceId ({ commit, dispatch, rootState }) {
		const user = $cookies.get('user')
		commit('setDeviceId', user)

	},
	saveDeviceId ({ commit, dispatch, rootState }) {
		$cookies.set('user', 'userxulei');
	},

  }
})
