<template>
  <v-layout
    v-touch="{
      left: () => swipe('Left'),
      right: () => swipe('Right'),
      up: () => swipe('Up'),
      down: () => swipe('Down')
    }"
    column
    align-center
    justify-center
    style="height: 500px"
    class="grey lighten-2"
  >
    <v-subheader>Swipe Direction Test</v-subheader>
    {{ swipeDirection }}

    <v-subheader>{{deviceId}}</v-subheader>
    <v-btn @click="saveDeviceId">save device id</v-btn>
  </v-layout>
</template>

<script>

import { mapState, mapGetters, mapActions } from 'vuex'  
export default {
  data: () => ({
    swipeDirection: 'None'
  }),
  created: function () {
    this.loadDeviceId()
  },
  methods: {
    swipe (direction) {
      this.swipeDirection = direction
    },
    saveDeviceId(){
      this.saveDeviceId()
    },
    ...mapActions({
      saveDeviceId: 'saveDeviceId',
      loadDeviceId: 'loadDeviceId',
    })    
  },



  computed: {
    ...mapState({
      deviceId: state => state.deviceId,
    }),
  },
}
</script>